import React, { Component } from 'react';
import './MoneyForm.scss';
import axios from 'axios';

export class MoneyForm extends Component {
	constructor(props) {
		super(props);
		this.state = { 
			message:[`구매 가격을`,`입력해 주세요`],
			value: '0000000'};
		this.handleChange = this.handleChange.bind(this);
		this.handleClick = this.handleClick.bind(this);
		this.handleSubmit = this.handleSubmit.bind(this);
	}

	handleChange(e) {
		this.setState({value: e.target.value.replace(/\D/,'')});		
	}

	handleClick(e){
		this.setState({value:''})
	}

	handleSubmit(e) {
		let postMoney = axios({
			method: 'post',
			url: '/api/money',
			data: {
			  money :this.state.value
			}
		  }) .then((response)=>{
			console.log(response);
			this.props.history.push(`/manual`);
		  });

		// let getMoney = axios.get('/api/money').then(function (response) {
		// 	console.log(response.data);
		// 	console.log(response.status);
		// 	console.log(response.statusText);
		// 	console.log(response.headers);
		// 	console.log(response.config);
		//   });
		e.preventDefault();
	}
	render() {
		return (
			<div id="money-form-con">
				<form onSubmit={this.handleSubmit} id="money-form">
					<label>
						<p>{this.state.message[0]}</p> <p>{this.state.message[1]}</p>
						<div className = "line"></div>
						<input id = "money-input"type="text"  value={this.state.value} onClick={this.handleClick} onChange={this.handleChange}/>
					</label>
					<span>원</span>
					</form>

					<div className= "button-circle">
					<button type="submit"  form = "money-form" value="Submit" >
					<span>구매</span>
					</button>
					</div>
			
			</div>
		);
	}
}

export default MoneyForm;
