import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import * as PIXI from 'pixi.js';
import MoneyForm from '../component/MoneyForm';
import ManualForm from '../component/ManualForm';
import NumberForm from '../component/NumberForm';
import WinningForm from '../component/WinningForm';
import Result from '../component/Result'
import History from '../component/History'

import './Container.css';
import AnimateCircle from '../component/AnimateCircle';
export class Container extends Component {
	render() {
		return (
			<div id="container">
				<Router>
                    <Route exact path="/" component ={MoneyForm}/>
                    <Route path = "/manual" component = {ManualForm}/>
                    <Route path = "/manualnumber/:amount" component = {NumberForm}/>
                    <Route path = "/winning" component ={WinningForm}/>
                    <Route path = "/result" component ={Result}/>
                    <Route path = "/history" component ={History}/>

					<AnimateCircle />
				</Router>
			</div>
		);
	}
}
export default Container;
