import React, { Component } from 'react';
import './WinningForm.scss';
import axios from 'axios';
import { thisExpression } from '@babel/types';

export class WinningForm extends Component {
	constructor(props) {
		super(props);
		this.state = {
			message: [ `당첨 번호`,`보너스 번호` ],
			winningNumber:0,
			bonusNumber:0
		};
		this.handleChangeNum = this.handleChangeNum.bind(this);
		this.handleChangeBonus = this.handleChangeBonus.bind(this);
		this.handleSubmit = this.handleSubmit.bind(this);
	}

	handleChangeNum(e) {
		this.setState({winningNumber: e.target.value});	
	}

	handleChangeBonus(e) {
		this.setState({bonusNumber: e.target.value.replace(/\D/,'')});		
	}


	handleSubmit(e) {
		console.log(this.state.winningNumber);
		console.log(this.state.bonusNumber);

		let postPurchase = axios({
			method: 'post',
			url: '/api/winning',
			data: {
				winningNumber:this.state.winningNumber,
				bonusNumber:this.state.bonusNumber
			}
		}).then((response) => {
			console.log(response);
			this.props.history.push(`/result`);
		});
		e.preventDefault();
	}
	render() {
		return (
			<div id="money-form-con">
				<form onSubmit={this.handleSubmit} id="money-form">
					<label>
						<p>{this.state.message[0]}</p>
						<input className="number-input" name = "1" value={this.state.winningNumber} type="text" onChange= {this.handleChangeNum} />
						<div className="line" />
						<p>{this.state.message[1]}</p>
						<input className="number-input" name = "2" value={this.state.bonusNumber} type="text"  onChange= {this.handleChangeBonus} />
					</label>
				</form>

				<div className="button-circle">
					<button type="submit" form="money-form" value="Submit">
						<span>입력</span>
					</button>
				</div>
			</div>
		);
	}
}

export default WinningForm;
