import React, { Component } from 'react';
import './History.scss';
import axios from 'axios';

export class MoneyForm extends Component {
	constructor(props) {
		super(props);
		this.state = {
			message: [ `역대 기록` ],
			value: 0,
			lottoNumbers: [],
			winningNumber: [ 0, 0, 0, 0, 0, 0, 0 ],
			prize: [ 0, 0, 0, 0, 0, 0 ],
			winningRate: 0
		};
		this.handleChange = this.handleChange.bind(this);
		this.handleClick = this.handleClick.bind(this);
		this.handleSubmit = this.handleSubmit.bind(this);
	}
	handleChange(e) {
		this.setState({ value: e.target.value.replace(/\D/, '') });
	}

	handleClick(e) {
		this.setState({ value: '' });
	}

	handleSubmit(e) {
		let getHistory = axios({
			method: 'post',
			url: '/api/history',
			data: {
				round: this.state.value
			}
		}).then((response) => {
			this.setState({ prize: response.data.prize });
			this.setState({ winningNumber: response.data.winningNumber });
			this.setState({ winningRate: response.data.winningRate });
			this.setState({ lottoNumbers: response.data.lottoNumbers });

			console.log(response);
		});
		e.preventDefault();
	}
	createLottoNumber=()=>{
		let numbers = [];
		let lottoNumbers = this.state.lottoNumbers;
		for(let i =0; i < lottoNumbers.length; i++){
			numbers.push(<p className="history-text">{lottoNumbers[i].join('/')}</p>)
		}
		return numbers;
	}
	componentDidMount() {}
	render() {
		return (
			<div id="money-form-con">
				<form onSubmit={this.handleSubmit} id="money-form">
					<label>
						<p>{this.state.message[0]}</p> <p>{this.state.message[1]}</p>
						<div className="line" />
						<input
							id="money-input"
							type="text"
							value={this.state.value}
							onClick={this.handleClick}
							onChange={this.handleChange}
						/>
					</label>
					<span>회</span>
				</form>
				<div className="button-circle">
					<button type="submit" form="money-form" value="Submit">
						<span>조회</span>
					</button>
				</div>
				<p className="history-text">구입한 로또 번호</p>
				<p className="history-text">{this.createLottoNumber()}</p>
			
				<p className="history-text">당첨 번호</p>
				<p className="history-text">{this.state.winningNumber[0]}</p>
				<p className="history-text">{this.state.winningNumber[1]}</p>
				<p className="history-text">{this.state.winningNumber[2]}</p>
				<p className="history-text">{this.state.winningNumber[3]}</p>
				<p className="history-text">{this.state.winningNumber[4]}</p>
				<p className="history-text">{this.state.winningNumber[5]}</p>

				<p className="history-text">보너스 번호</p>
				<p className="history-text">{this.state.winningNumber[6]}</p>
				<p className="history-text">당첨 결과</p>
				<p className="history-text">1등 : {this.state.prize[0]}개</p>
				<p className="history-text">2등 : {this.state.prize[1]}개</p>
				<p className="history-text">3등 : {this.state.prize[2]}개</p>
				<p className="history-text">4등 : {this.state.prize[3]}개</p>
				<p className="history-text">5등 : {this.state.prize[4]}개</p>
				<p className="history-text">꽝 : {this.state.prize[5]}개</p>
				<p className="history-text">수익률</p>
				<p className="history-text">{this.state.winningRate}</p>
			</div>
		);
	}
}

export default MoneyForm;
