import React, { Component } from 'react';
import './AnimateCircle.scss';
import circleBar from '../assets/circleBar.svg';
import anime from 'animejs';

export class AnimateCircle extends Component {
	componentDidMount() {
		const logEl = document.querySelector('#bar');
		anime({
			targets: logEl,
			rotate: 720,
			duration: 3000,
			loop: true,
			elasticity: 600,
			easing: 'easeOutElastic',
			delay: function(el, index) {
				return index * 80;
			}
		});
	}
	render() {
		return (
			<div id="animate-circle-con">
				<img id="bar" src={circleBar} />
			</div>
		);
	}
}
export default AnimateCircle;
