import React, { Component } from 'react';
import './ManualForm.scss';
import axios from 'axios';

export class ManualForm extends Component {
	constructor(props) {
		super(props);
		this.state = { 
			message:['수동 번호' ,'갯수 입력'],
			value: '0'};
		this.handleChange = this.handleChange.bind(this);
		this.handleClick = this.handleClick.bind(this);
		this.handleSubmit = this.handleSubmit.bind(this);
	}

	handleChange(e) {
		this.setState({value: e.target.value.replace(/\D/,'')});		
	}

	handleClick(e){
		this.setState({value:''})
	}

	handleSubmit(e) {
		this.props.history.push(`/manualnumber/${this.state.value}`);
		e.preventDefault();
	}
	render() {
		return (
			<div id="money-form-con" class = "manual-form-con">
				<form onSubmit={this.handleSubmit} id="money-form">
					<label>
						<p>{this.state.message[0]}</p> <p>{this.state.message[1]}</p>
						<div className = "line"></div>
						<input id = "money-input"type="text"  value={this.state.value} onClick={this.handleClick} onChange={this.handleChange}/>
					</label>
					<span>개</span>
					</form>

					<div className= "button-circle">
					<button type="submit"  form = "money-form" value="Submit" >
					<span>다음</span>
					</button>
					</div>
			
			</div>
		);
	}
}

export default ManualForm;
