import React, { Component } from 'react';
import './NumberForm.scss';
import axios from 'axios';
import { thisExpression } from '@babel/types';

export class NumberForm extends Component {
	constructor(props) {
		super(props);
		this.state = {
			message: [ `수동 번호 입력` ],
			amount:this.props.match.params.amount
		};
		let _array_ = [];
		for (let i = 0 ; i < this.state.amount; i ++){
			_array_.push(0);
		}
		this.state.value =_array_;		
		this.handleChange = this.handleChange.bind(this);
		this.handleClick = this.handleClick.bind(this);
		this.handleSubmit = this.handleSubmit.bind(this);
	}

	createForm = () => {
		let forms = [];
		for (let i = 0; i < this.state.amount; i++) {
			forms.push(<input id="number-input" name = {i} value={this.state.value[i]} type="text" onClick={this.handleClick} onChange= {this.handleChange} />)
		}
		return forms;	
	};

	handleChange(e) {
		let _value_ = this.state.value;
		_value_[e.target.name] = e.target.value;
		this.setState({value : _value_}) ;
	}

	handleClick(e) {
		let _value_ = this.state.value;
		_value_[e.target.name] = '';
		this.setState({value : _value_}) ;
	}

	handleSubmit(e) {
		console.log(this.state.value);
		let postPurchase = axios({
			method: 'post',
			url: '/api/purchase',
			data: {
				manualAmount:this.state.amount,
				manualNumbers:this.state.value
			}
		}).then((response) => {
			console.log(response);
			this.props.history.push(`/winning`);
		});
		e.preventDefault();
	}
	render() {
		return (
			<div id="money-form-con">
				<form onSubmit={this.handleSubmit} id="money-form">
					<label>
						<p>{this.state.message[0]}</p> <p>{this.state.message[1]}</p>
						<div className="line" />
						{this.createForm()}
					</label>
				</form>

				<div className="button-circle">
					<button type="submit" form="money-form" value="Submit">
						<span>구매</span>
					</button>
				</div>
			</div>
		);
	}
}

export default NumberForm;
