import React, { Component } from 'react';
import './Result.scss';
import axios from 'axios';

export class MoneyForm extends Component {
	constructor(props) {
		super(props);
		this.state = {
			message: [ `당첨 정보`, `이득률` ],
			prize: [ 0, 0, 0, 0, 0,],
			winningRate: 0
		};
		this.handleChange = this.handleChange.bind(this);
		this.handleClick = this.handleClick.bind(this);
		this.handleSubmit = this.handleSubmit.bind(this);
	}

	handleChange(e) {
		this.setState({ value: e.target.value.replace(/\D/, '') });
	}

	handleClick(e) {
		this.props.history.push(`/history`);
	}

	handleSubmit(e) {
		e.preventDefault();
	}

	postMoney = () => {
		axios({
			method: 'get',
			url: '/api/result'
		}).then((response) => {
			this.setState({ prize: response.data.prize });
			this.setState({ winningRate: response.data.winningRate });
		});
	};
	componentDidMount() {
		this.postMoney();
	}
	render() {
		return (
			<div id="money-form-con">
				<p>{this.state.message[0]}</p>
				<p className = "rank" >꽝 : {this.state.prize[0]}개</p> 
				<p className = "rank" >5등 : {this.state.prize[1]}개</p> 
				<p className = "rank" >4등 : {this.state.prize[2]}개</p> 
				<p className = "rank" >3등 : {this.state.prize[3]}개</p> 
				<p className = "rank" >2등 : {this.state.prize[4]}개</p> 
				<p className = "rank" >1등 : {this.state.prize[5]}개</p> 
				<div className="line" />
				<p>{this.state.message[1]}</p>
				<p>{this.state.winningRate}</p>

				<div className="button-circle">
					<button onClick={this.handleClick} id= "result_button" type="submit" form="money-form" value="Submit">
						<span id ="result_button_span">히스토리</span>
					</button>
				</div>
			</div>
		);
	}
}

export default MoneyForm;
